    color: #e6e6e6;
    padding: 10px 27px 10px 27px;
}

#toggle-all:checked:before {
    color: #737373;
}

#favorite-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

#favorite-list li {
    position: relative;
    font-size: 24px;
    border-bottom: 1px solid #ededed;
}

#favorite-list li:last-child {
    border-bottom: none;
}

#favorite-list li.editing {
    border-bottom: none;
    padding: 0;
}

#favorite-list li.editing .edit {
    display: block;
    width: 506px;
    padding: 13px 17px 12px 17px;
    margin: 0 0 0 43px;
}

#favorite-list li.editing .view {
    display: none;
}

#favorite-list li .toggle {
    text-align: center;
    width: 40px;
    /* auto, since non-WebKit browsers doesn't support input styling */
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    border: none; /* Mobile Safari */
    -webkit-appearance: none;
    appearance: none;
}

#favorite-list li .toggle:after {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

#favorite-list li .toggle:checked:after {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

#favorite-list li label {
    white-space: pre;
    word-break: break-word;
    padding: 15px 60px 15px 15px;
    margin-left: 45px;
    display: block;
    line-height: 1.2;
    transition: color 0.4s;
}

#favorite-list li.completed label {
    color: #d9d9d9;
    text-decoration: line-through;
}

#favorite-list li .destroy {
    display: none;
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    width: 40px;
    height: 40px;
    margin: auto 0;
    font-size: 30px;
    color: #cc9a9a;
    margin-bottom: 11px;
    transition: color 0.2s ease-out;
}

#favorite-list li .destroy:hover {
    color: #af5b5e;
}

#favorite-list li .destroy:after {
    content: '×';
}

#favorite-list li:hover .destroy {
    display: block;
}

#favorite-list li .edit {
    display: none;
}

#favorite-list li.editing:last-child {
    margin-bottom: -1px;
}

#footer {
    color: #777;
    padding: 10px 15px;
    height: 20px;
    text-align: center;
    border-top: 1px solid #e6e6e6;
}

#footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
    0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

#favorite-count {
    float: left;
    text-align: left;
}

#favorite-count strong {
    font-weight: 300;
}

#filters {
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    right: 0;
    left: 0;
}

#filters li {
    display: inline;
}

#filters li a {
    color: inherit;
    margin: 3px;
    padding: 3px 7px;
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 3px;
}

#filters li a.selected,
#filters li a:hover {
    border-color: rgba(175, 47, 47, 0.1);
}

#filters li a.selected {
    border-color: rgba(175, 47, 47, 0.2);
}

#clear-completed,
html #clear-completed:active {
    float: right;
    position: relative;
    line-height: 20px;
    text-decoration: none;
    cursor: pointer;
    position: relative;
}

#clear-completed:hover {
    text-decoration: underline;
}

#info {
    margin: 65px auto 0;
    color: #bfbfbf;
    font-size: 14px;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.85);
    text-align: center;
}

#info p {
    line-height: 1;
}

#info a {
    color: inherit;
    text-decoration: none;
    font-weight: 400;
}

#info a:hover {
    text-decoration: underline;
}

/*
	Hack to remove background from Mobile Safari.
	Can't use it globally since it destroys checkboxes in Firefox
*/
@media screen and (-webkit-min-device-pixel-ratio:0) {
    #toggle-all,
    #favorite-list li .toggle {
        background: none;
    }

    #favorite-list li .toggle {
        height: 40px;
    }

    #toggle-all {
        -webkit-transform: rotate(90deg);
        transform: rotate(90deg);
        -webkit-appearance: none;
        appearance: none;
    }
}

@media (max-width: 430px) {
    #footer {
        height: 50px;
    }

    #filters {
        bottom: 10px;
    }
